.side-bar {
   position: fixed;
   left: 0%;
   padding: 10px;
   flex-direction: column;
   background-color: rgba(255, 255, 255, 0.3);
   height: 100%;
   display: flex;
   justify-content: center;
   text-align: left;

   .title {
      margin-bottom: 10px;
      text-decoration: underline;
   }
   span {
      font-size: 10px;
   }

   p {
      margin-bottom: 6px;
   }
}
