.video-container {
   position: relative;
   display: flex;
   height: max-content;
   justify-content: center;
   align-items: center;
   gap: 10px;
   margin-top: 10px;

   .me,
   .user {
      display: flex;
      flex-direction: column;

      p {
         font-size: 18px;
         font-family: Livvic-Bold;
      }
   }

   video {
      width: 500px;
      border: 5px solid #121212;
      border-radius: 8px;
      margin-top: 10px;
   }

   .hidden {
      display: none;
   }
}
